import { PieChartComponent } from '@/components/charts/PieChart';
import UnderlineHeading from '@/components/heading/UnderlineHeading';
import { CustomSelect } from '@/components/dropDown/Dropdown';
import { useEffect, useState } from 'react';
import { ChartConfig } from '@/components/ui/chart';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import List from '@/features/car/components/statistics/List';
import { AttendanceDashboardChart } from '../../types/attendanceStats.types';

export default function AttendanceStats() {
    const [stats, setStats] = useState<AttendanceDashboardChart | null>(null);
    const [selectedValue, setSelectedValue] = useState('');

    useEffect(() => {
        const fetchStats = async () => {
            try {
                const res = await apiGet({
                    endpoint: endpoints.employeeAttendance.analysis,
                    // queryParams: { period: selectedValue },
                });
                setStats(res.data.data);
            } catch (err) {
                setStats(null);
            }
        };
        fetchStats();
    }, [selectedValue]);

    let chartData: any[] = [];
    let data: string[] = ['حضور', 'غياب', 'اجازة'];

    if (stats) {
        chartData = Object.entries(stats).map(([key, value]) => {
            if (key !== 'rented') {
                return {
                    days: key,
                    visitors: value,
                    fill: `var(--color-${key})`,
                };
            }
        });

        data = [`${stats.present} حضور`, `${stats.absent} غياب`, `${stats.leave} اجازة`];
    }

    const chartConfig = {
        visitors: {
            label: 'Visitors',
        },
        available: {
            label: 'حضور',
            color: 'hsl(var(--chart-1))',
        },

        maintenance: {
            label: 'اجازة',
            color: 'hsl(var(--chart-4))',
        },
        broken: {
            label: 'غياب',
            color: 'hsl(var(--chart-5))',
        },
    } satisfies ChartConfig;

    return (
        <div className="flex flex-col justify-between p-4 shadow-[0px_2px_12px_0px_#14142B14] rounded-[0.5rem]">
            <div className="flex flex-row justify-between mb-4">
                <UnderlineHeading text="الحضور" />
                <CustomSelect
                    options={[
                        { label: 'يومي', value: 'يومي' },
                        { label: 'أسبوعي ', value: 'أسبوعي ' },
                        { label: 'شهري', value: 'شهري' },
                        { label: 'سنوي', value: 'سنوي' },
                    ]}
                    value={selectedValue}
                    onValueChange={setSelectedValue}
                    placeholder="القائمة"
                    width="min-w-[101px]"
                />
            </div>
            <div className="flex flex-row items-center justify-between mb-8">
                <List text={data} />
                <PieChartComponent chartData={chartData} chartConfig={chartConfig} />
            </div>
        </div>
    );
}
