import DepartureList from '../components/departureList/DepartureList';
import EmployeesList from '../components/employeesList/EmployeesList';
import SalariesList from '../components/salariesList/SalariesList';
import AverageSalaries from '../components/statistics/AverageSalaries';
import StatisticsCardsComponent from '../components/statistics/StatisticsCardsComponent';
import AttendanceStats from '../components/statistics/AttendanceStats';

const HumanResourcesPage = () => {
    return (
        <div className="mt-6 mb-24 w-full">
            <StatisticsCardsComponent />
            <div className="mt-6 grid custom-grid-big gap-6 justify-center sm:justify-between">
                <AttendanceStats />
                <AverageSalaries />
            </div>
            <EmployeesList />
            <DepartureList />
            <SalariesList />
        </div>
    );
};

export default HumanResourcesPage;
